@import "../../core/styles/themes/default";

@drawer-prefix-cls: ~"@{ant-prefix}-drawer";

.@{drawer-prefix-cls} {
    position: fixed;
    left: 0; // add by bzx
	top: 0;
	width: 0%;
	height: 100%;
	z-index: @zindex-modal;
	> * {
		transition: transform @animation-duration-slow @ease-base-in;
	}

	&-content-wrapper {
		position: fixed;
	}
	.@{drawer-prefix-cls}-content {
		width: 100%;
		height: 100%;
	}
	&-left,
	&-right {
		width: 0%;
		height: 100%;
		.@{drawer-prefix-cls}-content-wrapper {
			height: 100%;
		}
		&.@{drawer-prefix-cls}-open {
			width: 100%;
		}
		&.@{drawer-prefix-cls}-open.no-mask {
			width: 0%;
		}
	}
	&-left {
		&.@{drawer-prefix-cls}-open {
			.@{drawer-prefix-cls}-content-wrapper {
				box-shadow: @shadow-1-right;
			}
		}
	}
	&-right {
		.@{drawer-prefix-cls} {
			&-content-wrapper {
				right: 0;
			}
		}
		&.@{drawer-prefix-cls}-open {
			.@{drawer-prefix-cls}-content-wrapper {
				box-shadow: @shadow-1-left;
			}
		}
	}

	&-top,
	&-bottom {
		width: 100%;
		height: 0%;
		.@{drawer-prefix-cls}-content-wrapper {
			width: 100%;
		}
		&.@{drawer-prefix-cls}-open {
			height: 100%;
		}
		&.@{drawer-prefix-cls}-open.no-mask {
			height: 0%;
		}
	}
	&-top {
		&.@{drawer-prefix-cls}-open {
			.@{drawer-prefix-cls}-content-wrapper {
				box-shadow: @shadow-1-down;
			}
		}
	}
	&-bottom {
		.@{drawer-prefix-cls} {
			&-content-wrapper {
				bottom: 0;
			}
		}
		&.@{drawer-prefix-cls}-open {
			.@{drawer-prefix-cls}-content-wrapper {
				box-shadow: @shadow-1-up;
			}
		}
	}
	&.@{drawer-prefix-cls}-open {
		.@{drawer-prefix-cls} {
			&-mask {
				opacity: 0.3;
				height: 100%;
				animation: antdDrawerFadeIn @animation-duration-slow @ease-base-out;
				transition: none;
			}
		}
	}

	&-title {
		margin: 0;
		font-size: @font-size-lg;
		line-height: 22px;
		font-weight: 500;
		color: @heading-color;
	}

	&-content {
		position: relative;
		background-color: @component-background;
		border: 0;
		background-clip: padding-box;
		z-index: 1;
	}

	&-close {
        cursor: pointer;
		border: 0;
		background: transparent;
		font-weight: 700;
		line-height: 1;
		text-decoration: none;
		transition: color @animation-duration-slow;
		color: @text-color-secondary;
		outline: 0;
        padding: 0;
        
        &-wrapper {
            position: absolute;
            right: 0;
            top: 0;
            z-index: 10;
            cursor: pointer;
        }

		&-x {
			display: block;
			font-style: normal;
			text-align: center;
			text-transform: none;
			text-rendering: auto;
			width: 56px;
			height: 56px;
			line-height: 56px;
			font-size: @font-size-lg;
		}

		&:focus,
		&:hover {
			color: #444;
			text-decoration: none;
		}
	}

	&-header {
		padding: 16px 24px;
		border-radius: @border-radius-base @border-radius-base 0 0;
		background: @component-background;
		color: @text-color;
		border-bottom: @border-width-base @border-style-base @border-color-split;
	}

	&-body {
		padding: 24px;
		font-size: @font-size-base;
		line-height: @line-height-base;
		word-wrap: break-word;
	}

	&-mask {
		position: fixed;
		width: 100%;
		height: 0;
		opacity: 0;
		background-color: @modal-mask-bg;
		filter: ~"alpha(opacity=50)";
		transition: opacity @animation-duration-slow linear, height 0s ease @animation-duration-slow;
	}
	&-open {
		transition: transform @animation-duration-slow @ease-base-out;
		&-content {
			box-shadow: @shadow-2;
		}
    }
    
    &-footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        border-top: 1px solid #e8e8e8;
        background-color: #fff;
    }
}

@keyframes antdDrawerFadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 0.3;
	}
}

@primary-color: #1890ff;